
.card {
    font-family: "Montserrat", sans-serif;
    background: $c-white;
    border-radius: 4px;
    border: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: $box-shadow-big;

    display: grid;
    grid-template-columns: 435px auto;
    //grid-template-columns: 1fr 1.3fr;
    grid-template-rows: 1fr;
    padding: 50px;
    margin: 0 1% 3% 1%;

    transition: width 1s, height 1s;

    //grid-auto-flow: row dense;
    h2 {
        font-size: $f-size-lg;
        font-weight: 600;
        margin-bottom: 30px;
    }
    ul {
        //list-style-type: disc;
        //padding-left: 24px;
    }

    ul > li {
        opacity: 0.9;
        margin-bottom: 4px;
    }
 
}
.card__technologies {
    font-family: "Exo", sans-serif;
    display: block;
}
.card__technologies--short {
    display: none;
    font-family: "Exo", sans-serif;
}

.card__buttons {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 24px;
    margin-top: 40px;
    max-width: 400px;

    .button {
        box-sizing: border-box;
        width: 100%;
        padding: 0.8em 0;
    }
}
.card__content {
    margin-left: 15px;
    grid-column: 2/3;

    .card__summary {
        opacity: 0.7;
        margin-bottom: 32px;
        line-height: 1.4;
    }
}
.card__description {
    transition: height 0.5s ease-in-out;
    height: 1.5em;
    overflow: hidden;
    line-height: 1.5;
}
.card__description-hidden {
    line-height: 1.5;    
    visibility: hidden;
    position: absolute;
}
.card__show-more {
    display: block;
    cursor: pointer;
    color: blue;
    padding-top: 8px;
    margin-bottom: 40px;
    text-decoration: underline;
}
.card__img-container {
    grid-column: 1/2;
    margin-right: 50px;
    height: 0;
    overflow: hidden;
    padding-top: 50%;
    background: white;
    position: relative;
    box-shadow: 0px 3px 25px rgba(0, 0, 0, 0.33);
    transition: transform .5s ease-in-out;

    &:hover {
        //transform: scale(1.1);
    }
    
    > img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

@media screen and (max-width: 1100px){
    .card__technologies {
        display: none;
    }
    .card__technologies--short {
        display: block;
    }
    .card {
        box-sizing: border-box;
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
        padding: 25px;
        width: 48%;
        margin: 1%;
        float: left;

        h2 {
            margin-bottom: 20px;
        }
    }
    .card__img-container {
        grid-row: 1/2;
        margin: 5px 10px 40px 10px;
    }
    .card__content {
        grid-column: 1/2;
        grid-row: 2/3;
        margin: 0;
    }
    .card__buttons {
        margin-top: 30px;
        max-width: 100%;
    }
}

@media screen and (max-width: 740px){

    .card {
        width: 100%;
    }
}
