.hero__container {
    padding-top: 120px;
    background: linear-gradient(180deg, #FEFEFE 0%, #FDFDFD 65.1%, #DDD9E8 100%);
    //animation-name: fadeInBg;
    animation-duration: .7s;
    animation-delay: .2s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    font-family: "Muli", sans-serif;
}
.hero {
    //grid-gap: 32px;
    height: 510px;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 0 !important;

    .button {
        margin-right: 16px;
    }
}

.hero__illustration-container {
    margin-top: 320px;
    grid-column: 1/2;
    align-self: start;
    justify-self: start;
    position: fixed;
}

.illustration-part {
    position: absolute;
    opacity: 0;
}

.display {
    animation-name: fallDown;
    animation-duration: .7s;
    animation-delay: .6s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    margin-left: -70%;
    margin-top: -34%;
}

.mug {
    animation-name: fallDown;
    animation-duration: .7s;
    animation-delay: .5s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    margin-left: -26%;
    margin-top: -3%;
}
.keyboard {
    animation-name: fallDown;
    animation-duration: .8s;
    animation-delay: .3s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    margin-left: -58%;
    margin-top: 11.5%;
}
.paper {
    animation-name: fallDown;
    animation-duration: 1s;
    animation-delay: .4s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    margin-left: -80%;
    margin-top: 4%;
}
.mouse {
    animation-name: fallDown;
    animation-duration: .7s;
    animation-delay: .5s;
    animation-fill-mode: forwards;
    animation-timing-function: linear;
    margin-left: -18%;
    margin-top: 13%;
}
.hero__text-container {
    grid-column: 2/3;
    max-width: 520px;
    margin-top: 50px;
    z-index: 2;

    h1 {
        font-size: $f-size-lg2;
        font-weight: bold;
        padding: 16px 0;
    }
    p {
        font-size: $f-size-md2;
        line-height: 1.4;
        padding: 16px 0;
    }
}

@media screen and (max-width: 1200px){
    .hero {
        grid-template-columns: 0.8fr 1fr !important;
    }  
    .hero__text-container {
        margin-left: auto;
        margin-right: auto;
    }
    .hero__illustration-container {
        //position: absolute;
        right: 53%;
    }
}

@media screen and (max-width: $tablet-landscape-min-width){
    .hero {
        grid-template-columns: 0 1fr !important;
    }
    .hero__illustration-container {
        opacity: 0;
        display: none;
    }
    .hero__text-container {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
    }
}

@media screen and (max-width: $phone-max-width){
    .hero__text-container {
        max-width: 100%;
    }  
    .hero {
        height: 600px;
    }
}

@keyframes fallDown {
    0% {
        opacity: 1;
        transform: scale(1, 1);
        transform: translateY(-700px);
    }
    50% {
        transform: translateY(8px);
    }
    60% {
        transform: scale(1.2, 0.7);
    }
    70% {
        transform: translateY(-5px);
    }
    80% {
        transform: translateY(0px);
        transform: scale(1.05, 0.95);
    }
    90% {
        transform: scale(1, 1);

    }
    100% {
        opacity: 1;
    }
}

@keyframes fadeInBg {
    0% {
        background: darken($c-lightgray, 5);
    }
    100% {
        background: $c-lightgray;
    }
}
