.button {
    padding: 16px 32px;
    display: inline-block;
    text-align: center;
    cursor: pointer;
    font-family: "Muli", sans-serif;
    border-radius: 2px;


    i {
        margin: -4px 15px -4px -6px;
        font-size: 1.2em;

    }
    &.button--white {
        background: $c-white;
        box-shadow: 0px 5px 10px rgba(157, 156, 181, 0.4);
        transition: all .3s cubic-bezier(.25,.8,.25,1);
        &:hover {
            background: $c-black;
            color: $c-white;

            a > {
                color: $c-white;
            }
        }
    }
    &.button--cta--alt {
        background: $c-purple;
        font-weight: 700;
        box-shadow: 0px 5px 12px rgba(157, 156, 181, 0.72);
        transition: all .3s cubic-bezier(.25,.8,.25,1);

        > a {
            color: $c-white;
        }
        &:hover {
            background: darken($c-purple, 5);
            box-shadow: 0px 11px 19px rgba(157, 156, 181, 0.78);
            transform: translatey(-2px);
        }
    }

    &.button--cta {
        background: $c-green;
        font-weight: bold;
        box-shadow: 0px 6px 13px #71B5A4;
        transition: all .3s cubic-bezier(.25,.8,.25,1);
        font-family: "Muli", sans-serif;
        font-size: 17px;
        padding: 12px 36px;

        > a {
            color: $c-white;
        }
        &:hover {
            background: darken($c-green, 5);
            box-shadow: 0px 11px 19px rgba(157, 156, 181, 0.78);
            transform: translatey(-2px);
        }
    }

    &.button--round {
        border-radius: 32px;
    }

    &.button--secondary {
        background: none;
        border: 1px solid $c-black;
        font-family: "Exo", sans-serif;
        font-size: 16px;
        padding: 12px 26px;

        > a {
            color: $c-black;
        }
        transition: all .3s;
        &:hover {
            background: $c-black;
            color: $c-white;
            > a {
                color: $c-white;
            }
        }
    }
}