
@import "reset.scss";
@import "variables.scss";
@import "modules/buttons.scss";
@import "modules/navbar.scss";
@import "hero.scss";
@import "projects.scss";
@import "modules/card.scss";
@import "animate.css";

html {
    font-family: "Muli", sans-serif;
    //font-family: "Montserrat", sans-serif;
    //height: 100%;
    //width: 100%;
    //overflow-x: scroll;
    overflow-x: hidden;
}
body {
    //width: 100%;
    background: $c-lightgray;
    //height: 100%;
    overflow-x: hidden;
}
.content {
    max-width: 1100px;
    margin: 0 auto;
    padding: 0 32px;
    padding-bottom: 32px;
    overflow-y: hidden;
}
.header {
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: $f-size-lg2;
    font-size: 35px;
    padding-top: 32px !important;
    padding-bottom: 32px !important;
}
.education {
    padding-top: 80px;
    padding-bottom: 80px;
    h2 {
        margin-bottom: 16px;
    }
}

.contact {
    border-top: 1px solid rgba(0,0,0,0.1);
    padding: 100px 0 100px 0;
    text-align: center;
    h2 {
        margin-bottom: 16px;
    }
}


.f-size-sm { font-size: $f-size-sm; }
.f-size-md { font-size: $f-size-md; }
.f-size-md2 { font-size: $f-size-md2; }
.f-size-lg { font-size: $f-size-lg; }
.f-size-lg2 { font-size: $f-size-lg2; }
.f-size-xl { font-size: $f-size-xl; }
.f-size-xl2 { font-size: $f-size-xl2; }

.f-bold { font-weight: bold; }

.f-color-white {
    color: $c-white;
}

.f-exo {
    font-family: "Exo", sans-serif;
}

.f-montserrat {
    font-family: "Montserrat", sans-serif;
}

.text-red {color: red};
.text-orange {color: orange};
.text-green {color: $c-green};
.text-purple {color: $c-purple};

.bg-purple {
    background: $c-purple;
}

[data-scroll] {
    transition: all .5s;
    //transition: opacity .2s;
}

[data-scroll="in"] {
    opacity: 1;
    transform: translateX(0);
}
[data-scroll="out"] {
    opacity: 0;
    transform: translateX(100px);
}

@media screen and (max-width: $phone-max-width){
    .content {
        padding-left: 30px;
        padding-right: 30px;
    }
}

