.navbar__container {
    width: 100%;
    position: fixed;
    height: 148px;
    z-index: 3;
    transition: height 0.5s;

    &.is-scrolling {
        height: 64px;
        background: rgba(242, 241, 254, 0.726);
    }
    
}

.navbar {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: 1px auto;
    box-sizing: border-box;

    .navbar__logo {
        margin: auto auto auto 0;
        padding: 20px;
    }
    ul {
        margin: auto 0 auto auto;
    }
    ul > li {
        display: inline-block;
        padding: 20px;
        font-size: $f-size-md2;

        > a:hover {
            color: #555;
        }
    }
}
@media screen and (max-width: $phone-max-width){
    .navbar {
        ul {
            margin: auto auto auto auto;
        }

        padding: 0 !important;
    }
    .navbar__container {    
        height: 64px;
        //width: 100vw;
    }
}
