.projects__container {
    background: $c-violet;
    position: relative;

}
.projects__vector-container {
    position: absolute;
    top: -150px;
    height: 155px;
    width: 100%;
    object-fit: fill;
}

.projects {
    padding: 3% !important;
    // display: grid;
    // grid-template-columns: 1fr;
    // grid-gap: 32px;
    overflow-x: hidden;
}
.show-cards-button {
    padding: 20px 40px;
    //height: 50px;
    margin: 40px 0;
    margin-left: calc(50% - 75px);
}

@media screen and (max-width: $tablet-landscape-min-width) and (min-width: $phone-max-width) {
    // .projects {
    //     grid-template-columns: 1fr;
    //     max-width: 65% !important;
    // }
    .projects__vector-container {
        top: -100px !important;
        height: 100px;
    }
}

@media screen and (max-width: $phone-max-width){

}

@media screen and (max-width: 1200px) and (min-width: $tablet-landscape-min-width){
    .projects__container {
        img {
            top: 545px;
            height: 115px;
        }
    }
    .projects__vector-container {
        top: -130px !important;
        height: 130px;
        //object-fit: fill;
    }
    
    .projects {
        grid-template-columns: 1fr 1fr;

    }
}